.header {
  background-color: #eff3f9;
  display: grid;
  grid-area: header;
  grid-template-columns: 200px 200px 1fr;
  border-radius: 0 $rounded 0 0;
  padding-left: 30px;
}

.header__title {
  font-family: $primary_font;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  color: $primary_font_color;
  align-self: center;
}

.header__search-form {
  display: flex;
  max-height: 35px;
  align-self: center;
  grid-gap: 15px;
  border: none;
}

.header__search {
  border-radius: 50px;
  border: 0;
  padding: 0 10px;
}

.header__notification-btn {
  background: none;
  border: none;
}

.header__profile {
  display: flex;
  justify-self: end;
  align-self: center;
  grid-gap: 10px;
  margin-right: 50px;
}

.header__user-name {
  font-family: $primary_font;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  display: flex;
  align-items: center;
  color: $primary_font_color;
}

.header__dropdown-btn {
  background: none;
  border: none;
}
